.contactContainer {
  background: -webkit-gradient(linear, left top, left bottom, from(#8e7161), to(#755361));
  background: -webkit-linear-gradient(#8e7161, #755361);
  background: -o-linear-gradient(#8e7161, #755361);
  background: linear-gradient(#8e7161, #755361);
}

.contactContent {
    text-align: center;
}

.contactLogo {
    width: 10%;
    min-width: 200px;
    margin-bottom: 50px;
    position: relative;
    left: 20px; /* visually center the logo */
}

.contactEmail {
    margin-bottom: 50px;
}

.contactEmail p {
  color: white;
  font-size: 1.5em;
  letter-spacing: 3px;
  text-shadow: 1px 1px 3px black;
}

.contactLogoList {
    list-style: none;
    margin-bottom: 50px;
    padding: 0;
}

.contactLogoList li {
    display: inline;
    margin: 0 20px;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
    cursor: pointer;
    position: relative;
    bottom: 0;
    -webkit-transition: all 200ms;
    -o-transition: all 200ms;
    transition: all 200ms;
}

.contactLogoList li:hover {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
    bottom: 10px;
}

.contactLogoList img {
    width: 4%;
    min-width: 70px
}

.contactForm {
  width: 600px;
  max-width: 100%;
  margin: auto;
  padding: 50px;
  background-color: white;
  text-align: right;
  -webkit-box-shadow: 5px 5px 0 rgba(0,0,0,0.25);
          box-shadow: 5px 5px 0 rgba(0,0,0,0.25);
}

.contactForm .formIDs input,
.contactForm textarea {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  margin-bottom: 25px;
  border: none;
  border-bottom: 2px solid gray;
  font-family: monospace;
}

.contactForm .formIDs input:focus,
.contactForm textarea:focus {
  outline: 2px solid var(--darkBlue);
}

.contactForm textarea {
  height: 100px;
}

.formSubmit {
  background-color: var(--darkBlue);
  color: white;
  padding: 15px 30px;
  border: none;
  font-size: 1em;
  letter-spacing: 2px;
  cursor: pointer;
  -webkit-transition: background-color 200ms, top 50ms;
  -o-transition: background-color 200ms, top 50ms;
  transition: background-color 200ms, top 50ms;
  position: relative;
  top: 0;
}

.formSubmit:hover {
  background-color: #052935;
}

.formSubmit:active {
  top: 2px;
}

@media only screen and (max-width: 900px) {
  .contactEmail p {
    font-size: 1.25em;
  }
  .contactLogoList li {
    margin: 0 10px;
  }
}



