.homeContainer {
  background: -webkit-gradient(linear, left top, left bottom, from(#367e96), to(#1d4f89));
  background: -webkit-linear-gradient(#367e96, #1d4f89);
  background: -o-linear-gradient(#367e96, #1d4f89);
  background: linear-gradient(#367e96, #1d4f89);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

@media only screen and (max-height: 550px) {
  .homeContainer {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
}

.homeContent {
  position: relative;
  bottom: 20px; /* visual centering */
}

.pixelLogo {
  text-align: center;
  margin-bottom: 20px;
}

.pixelLogo img {
    width: 250px;
    -webkit-animation-name: spinIn;
            animation-name: spinIn;
    -webkit-animation-duration: 750ms;
            animation-duration: 750ms;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.speechBubble {
    position: relative; /* bubble arrow needs relative absolute positioning */
    background-color: white;
    border-radius: 20px;
    -webkit-box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 5px 5px 0 rgba(0, 0, 0, 0.2);
}

.speechBubble p {
  font-size: 1.25em;
  line-height: 1.5em;
  color: var(--darkBlue);
  text-align: center;
  padding: 40px;
}

.speechBubble:before { /* speech bubble arrow - create a triangle using a mix of visible and invisible borders */
    content: '';
    position: absolute;
    top: -30px;
    left: 55%; /* off center to line up with pixel logo mouth */
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    /* center the speech arrow */
    width: 0;
    height: 0;
    border: 15px solid transparent;
    border-bottom: 30px solid white;
    border-top: 0;
}

@media only screen and (max-width: 900px) {
  .speechBubble p {
    font-size: 1em;
    padding: 25px;
  }
}



